/* eslint-disable no-console */
if (typeof console === 'undefined' || typeof console.log === 'undefined') {
	if (!window.console) {
		window.console = {}
	}
	const noOp = () => {}
	const methods = ['log', 'debug', 'warn', 'info', 'error', 'assert', 'time', 'timeEnd']
	for (let i = 0; i < methods.length; i++) {
		console[methods[i]] = noOp
	}
}
